import React, { useState } from "react";
import { Link } from "react-router-dom";
import { links } from "./nav.links";
import { NavButtons } from "./navbuttons.component";
import { menu_button, triangle_up, x_symbol } from "../../assets/svg";

export const NavigationLinks = () => {
  // eslint-disable-next-line no-unused-vars
  const [heading, setHeading] = useState("Menu");
  const [subHeading, setSubHeading] = useState("");
  const [showHeading, setShowHeading] = useState(false);

  const toggleMenu = () => {
    console.log(showHeading);
    setShowHeading(!showHeading);
  };
  return (
    <>
      {links.map((link, index) => (
        <div key={index}>
          {/* Desktop Menu Stylye */}
          <div className="px-30 text-right md:cursor-pointer group block xl:hidden">
            {showHeading ? (
              <NavButtons onClick={toggleMenu} icon={x_symbol} size={25} />
            ) : (
              <NavButtons onClick={toggleMenu} icon={menu_button} size={45} />
            )}
            {showHeading && (
              <div className="absolute p-2 md:p-4 top-[70px] right-0 z-index-40">
                <div className="bg-[#f4f4f4] rounded-lg p-10 px-10 grid grid-cols-1 md:grid-cols-3 gap-2">
                  <img
                    src={triangle_up}
                    style={{ width: 79, height: 30 }}
                    className="absolute top-[2px] right-[25px] z-index-40"
                    alt="triangle"
                  />
                  {link.sublinks.map((mysublinks, index) => (
                    <div className="text-left" key={index}>
                      <div
                        className="pb-1 border-black flex font-light items-center text-[#000e14]"
                        style={{ borderBottomWidth: 1 }}
                      >
                        {mysublinks.icon}&nbsp;
                        <span>{mysublinks.Head}</span> &nbsp;
                        <span className="font-semibold">
                          {mysublinks.subHead}
                        </span>
                        &nbsp;&nbsp;&nbsp;
                      </div>

                      <ul className="text-left">
                        {mysublinks.sublink.map((slink, index) => {
                          return (
                            <li
                              className="text-black my-1 font-semibold text-xs"
                              key={index}
                            >
                              <Link to={slink.link} className="hover:text-blue">
                                {slink.name}
                              </Link>
                            </li>
                          );
                        })}
                      </ul>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
          {/* Table Menu Style - Burger Menu Icon */}
          <div className="space-x-5 md:space-x-10 xl:space-x-20 hidden xl:flex">
            {/* sublinks */}
            {link.sublinks.map((slinks, index) => (
              <div key={index}>
                <div className="">
                  <h1
                    className="text-light text-lg lg:text-xl font-semibold text-right py-2 flex justify-between hover:cursor-pointer items-center"
                    onMouseEnter={() =>
                      subHeading !== slinks.subHead
                        ? setSubHeading(slinks.subHead)
                        : setSubHeading("")
                    }
                  >
                    {slinks.subHead}
                  </h1>
                  {subHeading === slinks.subHead && (
                    <div className="absolute p-4 top-[80px] z-index-40 bg-[#f4f4f4] rounded-lg">
                      <div>
                        <img
                          src={triangle_up}
                          style={{ width: 45, height: 12 }}
                          className="absolute top-[-7px] right-[10px] z-index-40"
                          alt="triangle"
                        />
                        {slinks.sublink.map((slink) => (
                          <span className="py-1 text-[#000e14] flex flex-column">
                            <Link to={slink.link}>{slink.name}</Link>
                          </span>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
          {/* Mobile Menu Style - Side Drawer */}
        </div>
      ))}
    </>
  );
};
