import React from "react";
import { Route, Routes } from "react-router-dom";
import { Home, ComingSoon, PrivacyPolicy, Imprint } from "../pages";

import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../data";
import { Loader, TranslationData } from "../components";

const Router = (props) => {
  const { loading, error, data } = useQuery(GET_CONTENT);
  if (loading) return <Loader />;
  if (error) return <h1>Directus Error ;)</h1>;

  // eslint-disable-next-line no-unused-vars
  var isDeployed = data["site_configurations"][0]["deployment_status"];

  return (
    <div className="font-mono text-Tmd bg-almost-white w-screen">
      <TranslationData />
      <Routes>
        <Route index element={isDeployed ? <Home /> : <ComingSoon />} />
        <Route path="/" element={isDeployed ? <Home /> : <ComingSoon />} />
        {/* <Route path="team" element={<Team />} /> */}
        <Route path="privacy" element={<PrivacyPolicy />} />
        <Route path="imprint" element={<Imprint />} />
      </Routes>
    </div>
  );
};

export default Router;
