import React from "react";

import { code_braces } from "../../assets/svg";

import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";
import { Button1 } from "../../components";
export const Training = () => {
  const { data } = useQuery(GET_CONTENT);
  const site_config = data.site_configurations[0];
  var bg_image = site_config.training_bg_photo.id;
  bg_image = `${process.env.REACT_APP_DIRECTUS_URL}assets/${bg_image}?access_token=${process.env.REACT_APP_DIRECTUS_ACCESS_TOKEN}`;

  return (
    <section className="bg-training backdrop-blur-lg h-screen bg-cover bg-opacity-30 bg-no-repeat flex  flex-col justify-center p-4 md:p-6 lg:p-16 gap-8">
      <h2 className="text-white text-4xl md:text-6xl lg:text-8xl font-black">
        Training <br /> [at] CamSol
      </h2>
      <Button1 text={"View our remote trainings"} icon={code_braces} />
    </section>
  );
};
