import React from "react";
import { german_flag, english_flag, french_flag } from "../../assets/images";
import { useTranslation } from "react-i18next";

export const TranslationBar = () => {
  const { i18n } = useTranslation();
  return (
    <div className="flex my-auto">
      <div
        style={{
          transform: i18n.language === "de" ? `scale(1.2)` : `scale(1)`,
        }}
        onClick={() => i18n.changeLanguage("de")}
        title="German"
        className="mx-2 text-center p-auto rounded-full hover:cursor-pointer"
      >
        <img
          className="h-[35px] w-[35px] rounded-full"
          alt="german_flag"
          src={german_flag}
        />
      </div>
      <div
        style={{
          transform: i18n.language === "fr" ? `scale(1.2)` : `scale(1)`,
        }}
        onClick={() => i18n.changeLanguage("fr")}
        title="French"
        className="mx-2 text-center w-[35px] hover:cursor-pointer"
      >
        <img
          className="h-[35px] w-[35px] rounded-full"
          alt="french_flag"
          src={french_flag}
        />
      </div>
      <div
        style={{
          transform: i18n.language === "en" ? `scale(1.2)` : `scale(1)`,
        }}
        onClick={() => i18n.changeLanguage("en")}
        title="English"
        className="mx-2 text-center w-[35px] hover:cursor-pointer"
      >
        <img
          className="h-[35px] w-[35px] rounded-full"
          alt="english_flag"
          src={english_flag}
        />
      </div>
    </div>
  );
};
