/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect } from "react";
import { TranslationBar, ScrollToTop, Footer, NavBar } from "../components";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../data";
import { NavLink } from "react-router-dom";
import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";
import { back_button } from "../assets/images";
import { useTranslation } from "react-i18next";

const heading_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: 0.5 } },
  hidden: { opacity: 0, x: -100, transition: { duration: 0.5 } },
};

export const Imprint = () => {
  const { loading, error } = useQuery(GET_CONTENT);
  const { t } = useTranslation();

  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;

  const control = useAnimation();
  const [ref, inView] = useInView();

  useEffect(() => {
    if (inView) {
      control.start("visible");
    } else {
      control.start("hidden");
    }
  }, [control, inView]);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  console.log(t("imprint.content"));

  return (
    <div className="font-[Inter] flex flex-col justify-between bg-dark text-white pt-[90px]">
      <div className="mx-20">
        <motion.div
          variants={heading_variants}
          ref={ref}
          initial="hidden"
          animate={control}
          className="flex lg:flex-row flex-col justify-between"
        >
          <div className="flex lg:flex-row flex-col mb-4 lg:mb-1">
            <NavLink
              to="/"
              className={`font-bold font-serif lg:mb-0 mb-5 hover:text-gray-300`}
            >
              <img width="50px" src={back_button} alt="back" />
            </NavLink>
            <h1 className="font-bold font-sans drop-shadow-2xl shadow-dark text-4xl lg:ml-10 mx-1 text-almost-white">
              {t("imprint.title")}
            </h1>
          </div>
          <div className="mx-auto lg:mx-1">
            <TranslationBar />
          </div>
        </motion.div>
      </div>
      <div
        className="text-steel-black mx-20 my-20 leading-tight thisPage"
        dangerouslySetInnerHTML={{ __html: t("imprint.content") }}
      ></div>
      <Footer />
      <NavBar />
      <ScrollToTop />
    </div>
  );
};
