import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";
import { useTranslation } from "react-i18next";

export const TranslationData = () => {
  const { data } = useQuery(GET_CONTENT);

  const en_privacy_policy = data.privacy_policy[0];
  const de_privacy_policy = data.privacy_policy[1];
  const fr_privacy_policy = data.privacy_policy[2];

  const en_imprint = data.imprint[1];
  const de_imprint = data.imprint[0];
  const fr_imprint = data.imprint[2];

  // eslint-disable-next-line no-unused-vars
  const { t, i18n } = useTranslation();
  i18n.store.data = {
    en: {
      translation: {
        privacy_policy: {
          title: en_privacy_policy.title,
          content: en_privacy_policy.content,
        },
        imprint: {
          title: en_imprint.title,
          content: en_imprint.content,
        },
      },
    },
    de: {
      translation: {
        privacy_policy: {
          title: de_privacy_policy.title,
          content: de_privacy_policy.content,
        },
        imprint: {
          title: de_imprint.title,
          content: de_imprint.content,
        },
      },
    },
    fr: {
      translation: {
        privacy_policy: {
          title: fr_privacy_policy.title,
          content: fr_privacy_policy.content,
        },
        imprint: {
          title: fr_imprint.title,
          content: fr_imprint.content,
        },
      },
    },
  };

  return <></>;
};
