import React from "react";
import { useMountEffect } from "@react-hookz/web"; // cjs

export const EmbededMeetingForm = () => {
  useMountEffect(() => {
    const script = document.createElement("script");
    script.src =
      "https://static.hsappstatic.net/MeetingsEmbed/ex/MeetingsEmbedCode.js";
    script.async = true;
    document.body.appendChild(script);
  }, []);

  return (
    <div className="w-full p-10 2xl:mt-[140px]">
      <div
        class="meetings-iframe-container"
        data-src="https://meetings-eu1.hubspot.com/david-firmin?embed=true"
      ></div>
    </div>
  );
};
